/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  margin-top: 2rem;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  margin-top: 2rem;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-verde .olvidaste {
  color: rgb(255, 69, 0);
}

.boton-naranjo {
  background-color: rgb(255, 69, 0);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  margin-top: 2rem;
  border-radius: 1rem;
}
.boton-naranjo:hover {
  background-color: #e63e00;
  cursor: pointer;
}

.boton-rosa {
  background-color: #ff33ac;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  margin-top: 2rem;
}
.boton-rosa:hover {
  background-color: #ff1aa2;
  cursor: pointer;
}

.boton-azul {
  background-color: #001bff;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  margin-top: 2rem;
}
.boton-azul:hover {
  background-color: #0018e6;
  cursor: pointer;
}

.boton-blanco {
  background-color: #FFFFFF;
  color: black;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 5rem, 2;
  display: block;
  border: 1px solid;
}
.boton-blanco:hover {
  background-color: #e1e1e1;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  margin-top: 2rem;
  width: 100%;
  border: none;
}
.boton-rojo-block:hover {
  background-color: #a40606;
  cursor: pointer;
}

.boton-rojo {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  margin-top: 2rem;
  border: none;
}
.boton-rojo:hover {
  background-color: #a40606;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  margin-top: 2rem;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-video {
  background-color: rgb(189, 7, 7);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  margin-top: 2rem;
}
.boton-video:hover {
  background-color: #a40606;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode label, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .seccion.contenedor .anuncio {
  background-color: #333333;
}
.dark-mode .acciones a {
  color: #FFFFFF;
}
.dark-mode table .TD1 {
  color: #FFFFFF;
}
.dark-mode .formulario legend {
  color: #FFFFFF;
}
.dark-mode .header {
  background-color: #333333;
}
.dark-mode .footer {
  background-color: #333333;
}

/** UI **/
.header {
  background-color: hsl(220, 100%, 50%);
  padding: 1rem 0 -2rem 0;
  backdrop-filter: blur(8px);
  box-shadow: 0 30px 30px rgba(0, 0, 0, 0.2);
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .contenido-header {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
.contenido-header h1 {
  color: #FFFFFF;
  text-align: left;
  font-weight: 700;
  line-height: 0.5;
}

a {
  font-weight: 700;
}

.Titulo1 {
  background-color: rgb(255, 69, 0);
  font-weight: 700;
  color: #FFFFFF;
  border-radius: 1rem;
  border-width: 2px;
}

.Titulo2 {
  background-color: rgb(255, 69, 0);
  font-weight: 700;
  color: #FFFFFF;
  border-radius: 1rem;
}

.footer {
  padding: 5rem 0;
  background-color: hsl(220, 100%, 50%);
  margin: auto;
}
.footer .contenedor-footer {
  padding-top: 2.5rem 0;
  margin: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra .salmon {
  width: 100%;
}
@media (min-width: 768px) {
  .barra .salmon {
    width: 35%;
  }
}
.barra .h1S {
  display: flex;
  justify-content: center;
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  transition: all 0.5s ease;
}
.navegacion a:hover {
  padding: 5px 10px;
  background: rgb(255, 69, 0);
  color: #FFFFFF;
  border-radius: 15px;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/** Menu de hamburguesa**/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}
.mobile-menu img:hover {
  cursor: pointer;
}

.mostrar {
  visibility: visible;
  opacity: 3;
  height: auto;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 10rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario .forma-respuestas {
  max-width: 10rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=file] {
  display: none;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: grid;
    justify-content: space-around;
  }
}
.acciones a {
  padding-top: 2rem;
  color: #000000;
  display: block;
  margin-bottom: 2.5rem;
  text-align: justify;
  text-decoration: none;
}
.acciones a:hover {
  color: #71B100;
}

.boton-verde {
  border-radius: 1rem;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  padding: 1rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio .imagenes-anuncios {
  border: 1px solid;
}

.boton-rosa {
  border-radius: 1rem;
}

.boton-azul {
  border-radius: 1rem;
}

.boton-blanco {
  border-radius: 1rem;
}

h1, h2, h3 {
  font-weight: 400;
}

/** Paginas Internas **/
.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
  border-radius: 1rem;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}

table.usuarios {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 1;
}
table.usuarios thead {
  background-color: #71B100;
}
table.usuarios thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.usuarios tbody td {
  text-align: center;
  font-weight: 700;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
  border-radius: 1rem;
}
.alerta.error {
  background-color: rgb(189, 7, 7);
}
.alerta.exito {
  background-color: #71B100;
}