.formulario {
    p {
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0;
    }

    legend {
        font-size: 2rem;
        color: $grisOscuro;
    }

    label {
        font-weight: $bold;
        text-transform: uppercase;
        display: flex;
        justify-content: center;
        margin-bottom: 2rem;
    }
    input:not([type="submit"]), 
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $gris;
        border-radius: 1rem;
    }

    textarea {
        height: 20rem;
    }

    .forma-contacto {
        max-width: 10rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"] {
        margin: 0;
        width: auto;
    }

    .forma-respuestas{
        max-width: 10rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    input[type="file"] {
        display: none;
    }
}

.acciones {

    margin-top: $separacion;
    @include tablet {
        display: grid;
        justify-content: space-around;
    }

    a {
        padding-top: 2rem;
        color: $negro;
        display: block;
        margin-bottom: calc($separacion / 2);
        text-align: justify;
        text-decoration: none;

        &:hover {
            color: $verde;
        }
    }
}

.boton-verde {
    border-radius: 1rem;
}